@charset "UTF-8";
@font-face { font-family: "Lato"; src: url("../fonts/subset-Lato-Regular.woff2") format("woff2"), url("../fonts/subset-Lato-Regular.woff") format("woff"); font-weight: 400; font-style: normal; }

* { outline: none; margin: 0; padding: 0; -webkit-tap-highlight-color: transparent; /* отклчение подсветки на тач-устройствах */ }

html { height: 100%; -ms-touch-action: manipulation; touch-action: manipulation; -ms-overflow-style: scrollbar; box-sizing: border-box; font-size: 16px; /* Для svg */ }

*, *::before, *::after { box-sizing: inherit; }

ol, ul { list-style: none; }

table { border-collapse: collapse; border-spacing: 0; }

img { border: 0; vertical-align: bottom; max-width: 100%; height: auto; }

button::-moz-focus-inner, input::-moz-focus-inner { border: 0; padding: 0; }

input, button, textarea, select { resize: none; font-family: inherit; font-size: inherit; font-weight: inherit; border-radius: 0; box-shadow: none; color: inherit; }

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button { -webkit-appearance: none; margin: 0; }

input[type="number"] { -moz-appearance: textfield; }

input[type="number"]:hover, input[type="number"]:focus { -moz-appearance: number-input; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance: none; margin: 0; }

a { text-decoration: none; }

a:hover { text-decoration: none; }

.icon-cadesign { width: 0.53em; height: 1em; }

body { height: 100%; font-size: 14px; line-height: 1.3; font-family: "Lato", sans-serif; color: #333333; font-weight: normal; cursor: default; background: #fff; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; text-rendering: optimizeLegibility; }

.page { min-width: 320px; height: 100%; position: relative; display: flex; flex-direction: column; }

.header, .footer { flex: 0 0 auto; }

.main { flex: 1 0 auto; }

.container { width: 100%; max-width: 1920px; padding-left: 75px; padding-right: 75px; margin-left: auto; margin-right: auto; }

a { color: #cc3636; text-decoration: none; }

.icon { display: inline-block; fill: currentColor; position: relative; }

button { cursor: pointer; }

.heading-1 { font-weight: bold; }

.block { display: block; }

.flex { display: flex; }

.none { display: none; }

.row { display: flex; flex-wrap: wrap; margin-left: -15px; margin-right: -15px; }

.col { padding-left: 15px; padding-right: 15px; }

.text-center { text-align: center; }

.nowrap { white-space: nowrap; }

@media screen and (max-width: 1640px) { .xl-block { display: block; }
  .xl-flex { display: flex; }
  .xl-none { display: none; }
  .xl-text-center { text-align: center; } }

@media screen and (max-width: 1320px) { .lg-block { display: block; }
  .lg-flex { display: flex; }
  .lg-none { display: none; } }

@media screen and (max-width: 1023px) { .md-block { display: block; }
  .md-flex { display: flex; }
  .md-none { display: none; } }

@media screen and (max-width: 767px) { .sm-block { display: block; }
  .sm-flex { display: flex; }
  .sm-none { display: none; } }

.webgl { width: 100%; height: 100%; }
